.about_content p {
  margin: 0rem 0 2.6rem;
  /* color: var(--color-light); */
  text-shadow: 1px 0px #73a80a;
}
.links {
  display: flex;
  gap: 10px;
}

/* ================ MEDIA QUERIES (MEDIUM DEVICES) ================= */
@media screen and (max-width: 1024px) {
  .about_container {
    grid-template-columns: 1fr;
    gap: 0;
  }
  #about {
    padding-bottom: 0;
  }
  .about_me {
    width: 50%;
    margin: 1rem auto 2rem;
    aspect-ratio: 0;
  }

  .about_content p {
    margin: 1rem 0 1.5rem;
  }

  a.btn.btn-primary.btn-primary-left {
    background-color: var(--color-primary);
  }
}

/* ================ MEDIA QUERIES (SMALL DEVICES) ================= */
@media screen and (max-width: 600px) {
  .about_me {
    width: 65%;
    margin: 0 auto 3rem;
  }

  /* .about_cards {
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  } */

  .about_content {
    text-align: center;
  }

  .about_content p {
    margin: 1.5rem 0;
  }
}
